* {
    font-family: "Fontdiner Swanky", system-ui, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
    font-weight: 400;
    font-style: normal;
}

body {
    margin: 0;
    padding: 0;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    background-color: rgb(255, 255, 223);
}

header {
    font-size: 4rem;
    width: 100%;
    padding: 1rem;
    background-color: rgb(231, 255, 98);
    display: flex;
    justify-content: center;
    align-items: center;
    border-bottom: 3px solid black;
}

.container {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    min-height: 80vh;
    min-width: 100vw;
}

.city-title {
    font-size: 2rem;  
}

.weather-content {
    display: flex;
    flex-wrap: wrap;
    gap: 20px;
    padding: 10px;
}
  
.item {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    width: 300px;
    height: 300px;
    text-align: center;
    box-sizing: border-box;
    padding: 10px;
}
  
.item img {
    max-width: 100%;
    max-height: 100%;
    object-fit: contain;
    margin-top: 10px;
